/* CHAT STYLE */
.chat .nullRow{
  margin-left: 0!important;
  margin-right: 0!important;
  }
.chat #sala_chat{
  display: none;
}
.chat .window-chat{
  position: absolute;
  bottom: 0;
}
/* .panel-full{
  display: none;
} */
.chat .col-md-2, .col-md-10{
  padding:0;
}
.chat .panel{
  margin-bottom: 0px;
}
.chat .chat-window{
  bottom:0;
  position:fixed;
  float:right;
  margin-left:10px;
}
.chat .chat-window > div > .panel{
  border-radius: 5px 5px 0 0;
}
.chat .icon_minim{
  padding:2px 10px;
}
.chat .msg_container_base{
background: #e5e5e5;
margin: 0;
padding: 0 10px 10px;
max-height:300px;
overflow-x:hidden;
}
.chat .top-bar {
background: #666;
color: white;
padding: 10px;
position: relative;
overflow: hidden;
}
.chat .msg_receive{
  padding-left:0;
  margin-left:0;
}
.chat .msg_sent{
  padding-bottom:20px !important;
  margin-right:0;
}
.chat .messages {
background: white;
padding: 10px;
border-radius: 2px;
box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
max-width:100%;
}
.chat .messages > p {
  font-size: 13px;
  margin: 0 0 0.2rem 0;
}
.chat .messages > time {
  font-size: 11px;
  color: #ccc;
}
.chat .msg_container {
  padding: 10px;
  overflow: hidden;
  display: flex;
}
.chat img {
  display: block;
  width: 100%;
}
.chat .avatar {
  position: relative;
}
.chat .base_receive > .avatar:after {
  content: "";
  position: absolute;
  top: 17px;
  right: 0;
  width: 0;
  height: 0;
  border: 5px solid #FFF;
  border-left-color: rgba(0, 0, 0, 0);
  border-bottom-color: rgba(0, 0, 0, 0);
}

.chat .base_sent {
justify-content: flex-end;
align-items: flex-end;
}
.chat .base_sent > .avatar:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 0;
  border: 5px solid white;
  border-right-color: transparent;
  border-top-color: transparent;
  box-shadow: 1px 1px 2px rgba(black, 0.2);
}

.chat .msg_sent > time{
  float: right;
}



.chat .msg_container_base::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
  background-color: #F5F5F5;
}

.chat .msg_container_base::-webkit-scrollbar
{
  width: 12px;
  background-color: #F5F5F5;
}

.chat .msg_container_base::-webkit-scrollbar-thumb
{
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: #555;
}

.chat .btn-group.dropup{
  position:fixed;
  left:0px;
  bottom:0;
}
.chat .nullMarginPadding{
  margin : 0px!important;
  padding : 0px!important;
}
/* /CHAT STYLE */





